@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');

$font-monsterrat: 'Montserrat', sans-serif;

*{
    margin: 0;
    padding: 0;
}
body{
    font-family: $font-monsterrat;
    background-color: #e5e5e5;
}
.allCards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    .card{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        width: 19%;
        height: 35vh;
        background-color: #fefefe;
        border-radius: 10px;
        margin: 3% 1%;
        position: relative;
        box-sizing: border-box;
        box-shadow: 0px 0px 10px rgb(63, 63, 63);
        cursor: pointer;
        transition: 1s;
        &:hover{
            transform: scale(1.02);
        }
        img{
            width: 80%;
            height: 30vh;
            position: absolute;
            top: -15px;
            border-radius: 10px;
            box-shadow: 0px 5px 10px rgb(63, 63, 63);
        }
        h3{
            color: black;
            z-index: 5;
            padding: 5% 2%;
            font-size: 18px;
            text-align: center;
            font-weight: 400;
        }
        
    }
}
@media (max-width: 1150px){
    .allCards{
        .card{
            width: 19%;
            height: 33vh;
            img{
                width: 70%;
                height: 25vh;
            }
            h3{
                padding: 3% 2%;
            }
        }
    }
}
@media (max-width: 800px){
    .allCards{
        .card{
            width: 29%;
            height: 37vh;
            img{
                width: 85%;
                height: 28vh;
            }
            h3{
                padding: 10% 2%;
            }
        }
    }
}
@media (max-width: 500px){
    .allCards{
        .card{
            width: 85%;
            height: 50vh;
            margin: 6% 1%;
            img{
                width: 85%;
                height: 42vh;
            }
            h3{
                padding: 6% 2%;
            }
        }
    }
}